// ------------------------------------\
// NAVIGATION
// ------------------------------------/

// Top tasks
.a-bgWhite {
  .a-topTasks {
    background-color: $blue-lighter;

    .a-btn {
      border-bottom: 1px solid $blue-light;

      &:hover,
      &:focus {
        color: $black;
        background: $blue-light;
        border-bottom: 1px solid $blue-light;
      }

      &.a-active {
        background: $blue-light;
      }
    }
  }
}

.a-topTasks {
  padding: 0;
  margin: 0;

  div,
  span {
    &[class^='col'] {
      padding: 0;
      margin: 0;
    }
  }

  .a-btn {
    @include a-fontBold;
    @include a-fontSize16;

    min-height: $spacer * 6;
    padding: $spacer;
    margin-bottom: 0;
    text-align: center;
    border: none;
    border-bottom: 1px solid $grey-light;

    &:hover {
      border-bottom: 1px solid $grey-light;
    }
  }

  @include media-breakpoint-down( sm ) {
    .a-btn {
      display: block;
      padding: $spacer / 2;

      .a-btn-icon-symbol {
        margin-bottom: $spacer / 2;
      }

      span {
        padding: 0;
      }
    }
  }
}

.a-inline-page-link {
  display: block;

  .row {
    margin: 0;
  }

  div {
    &[class^='col-'] {
      padding: 0;
    }
  }

  .list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  a {
    @extend .a-fontSizeXS;
    @include a-fontBold;

    display: inline-block;
    padding: 0;
    color: $blue-dark;
    text-decoration: none;
    border: none;

    &.disabled {
      color: $grey;
    }
  }
}

.a-page-nav-dest {
  padding-top: $spacer;
  padding-bottom: $spacer / 2;

  a {
    border: none;
  }
}

.a-overflow-dropdown {
  display: block;
  padding: $spacer;
  border-bottom: 1px solid $grey-light;

  .a-charIcon {
    margin-top: -$spacer;
    margin-bottom: -$spacer;

    span {
      @include a-fontBold;
      @include a-fontSize24;
      font-style: normal;
    }
  }
}

a {
  &.a-overflow-dropdown {
    &:active,
    &:focus,
    &:hover {
      background: $grey-light;
      border-bottom: 1px solid $grey-light;
    }
  }
}

.a-dropdown-overflow-menu {
  right: 0;
  left: auto;
  width: 300px;

  &.a-dropdownTriangle {
    &::after {
      @include media-breakpoint-down( xs ) {
        left: 35%;
      }
    }
  }

  @include media-breakpoint-down( xs ) {
    right: -53px;
  }
}

.a-dropdown-overflow-menu-right {
  right: 0;
  left: auto;
  width: 300px;

  &.a-dropdownTriangle {
    &::after {
      @include media-breakpoint-only( xs ) {
        left: -25%;
      }

      @include media-breakpoint-only( sm ) {
        left: -28%;
      }

      @include media-breakpoint-only( md ) {
        left: -28%;
      }
    }
  }

  @include media-breakpoint-down( xs ) {
    right: -53px;
  }
}

// Nav tabs that looks like radio btns
.a-navTabsRadio {
  flex-direction: column;
  margin-top: $spacer;
  margin-bottom: $spacer * 3;
  border: none;

  .nav-link {
    padding: 0;
    border: none;

    .custom-control-indicator {
      top: 6px;
    }

    &.active {
      color: $black;

      .custom-control-label {
        &::after {
          top: 1px;
          left: 2px;
          width: 2rem;
          height: 2rem;
          background-image: url('data:image/gif;base64,');
        }
      }
    }
  }
}

//removing the dropdown-backdrop class bootstrap adds for closing the menu when tapping outside of it.
//causes problems in android and other windows sceens with tuch
.navbar-toggleable-sm {
  .dropdown-backdrop {
    @include media-breakpoint-down( md ) {
      display: none;
    }
  }
}
